.divplaceholder3,
.input6 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.divplaceholder3 {
  border: 0;
  font-family: Poppins;
  font-size: 20px;
  background-color: transparent;
  overflow: hidden;
  padding: 0 464px 0 0;
  justify-content: space-between;
}
.input6 {
  background-color: #fff;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  border-left: 1px solid #e5e5e5;
  padding: 17.5px 32px 20.5px 33px;
  justify-content: flex-start;
}
.search1 {
  position: relative;
  line-height: 23px;
  font-weight: 300;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 77.12px;
  height: 28px;
  flex-shrink: 0;
}
.button19 {
  flex: 1;
  background-color: #138ccc;
  height: 68px;
  flex-direction: column;
  padding: 19.5px 0;
  box-sizing: border-box;
}
.button19,
.certification-registary-page,
.form {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.form {
  background-color: transparent;
  box-shadow: 0 8px 20px -8px #000;
  width: 940px;
  overflow: hidden;
  flex-direction: row;
}
.certification-registary-page {
  position: relative;
  width: 100%;
  height: 810px;
  flex-direction: column;
  padding: 363px 0;
  box-sizing: border-box;
  background-image: url(../public/certification-registary-page@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: 20px;
